import React from 'react';
import { styled } from '../../modules/index';
import { Icon, Text, FlexBox } from '../../modules/globalStyle';
import { DialogWrapper } from '../Dialog/index';
import { observer, useLocalStore } from 'mobx-react-lite';
import { useStore } from '../../store/index';
import { injectedConnector, walletconnectConnector, walletLink } from '../../lib/web3-react-bsc';
import { useWeb3React } from '@web3-react/core';
import { isMobile, isIOS } from "react-device-detect";

const WalletBox = styled('div', {
  padding: '1.5rem 0 0.5rem 0',
  borderTop: '1px solid $borderPrimary',
  borderBottom: '1px solid $borderPrimary'
});

const WalletItem = styled('div', {
  backgroundColor: '$bg1',
  p: '1rem',
  mb: '1rem'
});

export const EthConnectWallet = observer(() => {
  const { god, lang } = useStore();
  const { activate, error, deactivate } = useWeb3React();

  const activateInjectedProvider = async (providerName) => {
    const { ethereum } = ( window as WindowChain ) ;

    if (!ethereum?.providers) {
      return undefined;
    }

    let cprovider;
    if (providerName == "CoinBase") {
      cprovider = ethereum.providers.find(
        ({ isCoinbaseWallet }) => isCoinbaseWallet
      );
    }
    if (providerName == "MetaMask") {
      cprovider = ethereum.providers.find(({ isMetaMask }) => isMetaMask);
    }
    console.log("providerName", providerName, cprovider);

    if (cprovider) {
      await ethereum.setSelectedProvider(cprovider);
    }
  };

  const connectToCoinbaseWallet = async () => {
    // if (isIOS){
    //   window.location.href = "https://go.cb-w.com/NEpf8qrlOqb";
    //   await activate(walletLink);
    //   setWalletInfoOnLocal(2);
    //   dispatch(Connector("Coinbase"));
    // }
    // removeLocalStorageData();
    try {
      activateInjectedProvider("CoinBase");
      await activate(walletLink);
    } catch (error) {
      console.log(error);
    }
  };

  const connectToMetaMask = async () => {
    if ((window as WindowChain).ethereum) {
      try {
        console.log("activate_metamask");
        activateInjectedProvider("MetaMask");
        await activate(injectedConnector);
        god.eth.connector.latestProvider.save('inject');
      } catch (error) {
        console.error(error);
      }
    } else if (isIOS) {
      window.location.href = "dapp://blackhole.messier.app";
      await activate(injectedConnector);
      god.eth.connector.latestProvider.save('inject');
    } else if (isMobile) {
      try {
        console.log("activate_metamask");
        // activateInjectedProvider("MetaMask");
        await activate(injectedConnector);
        alert('YesYes');
        god.eth.connector.latestProvider.save('inject');
      } catch (error) {
        alert('Not');
        window.location.href = "https://metamask.app.link/dapp/blackhole.messier.app";
        console.error(error);
      }
    } else {
      window.location.href = "https://metamask.io";
    }
  };

  const store = useLocalStore(() => ({
    get visible() {
      return god.eth.connector.showConnector;
    },
    close() {
      god.eth.connector.showConnector = false;
    },
    connectInejct() {
      activate(injectedConnector);
      god.eth.connector.latestProvider.save('inject');
    },
    connectWalletConnect() {
      activate(walletconnectConnector);
    },
  }));

  const content = (
    <WalletBox>
      <WalletItem onClick={connectToMetaMask} css={{ cursor: 'pointer' }} >
        <FlexBox justify="between" >
          <Text size="small" weight="semibold" color="white" css={{ cursor: 'pointer' }}>
            {lang.t('metamask')}
          </Text>
          <Icon src="/images/metamask.svg"></Icon>
        </FlexBox>
      </WalletItem>
      <WalletItem onClick={connectToCoinbaseWallet} css={{ cursor: 'pointer' }}  >
        <FlexBox justify="between" >
          <Text size="small" weight="semibold" color="white" css={{ cursor: 'pointer' }}>
            {lang.t('coinbase')}
          </Text>
          <Icon src="/images/coinbaseWalletIcon.svg"></Icon>
        </FlexBox>
      </WalletItem>
    </WalletBox>
  );
  return <DialogWrapper title="Connect to a wallet" size="base" content={content} visible={store.visible} close={store.close} />;
});
